@import url(./index.css);
:root{
  --scrolling: 0;
  --anim-item-pos:0px;
}

.anim{
  width: 100vw;
  height: 100vh;
}

.item-anim{
  position: absolute;
  top:  var(--anim-item-pos);
  height: 150px;
  width: 150px;
  border:  solid red 1px;

  
  animation: animar 1s linear infinite;
  animation-delay: calc( var(--scrolling) *1s);
  animation-play-state: paused;
  animation-iteration-count: 1;
  animation-fill-mode: both;
}
@keyframes animar {
  0%{top: var(--anim-item-pos)}
  100%{--anim-item-pos: 300px; top: var(--anim-item-pos)}
}


.slider-machine {
  width: 100%;
  height: 100%;
  display: none;
  opacity: 0;
}
.swiper-wrapper{
  margin-left: 60px;
}
.slider-machine .item-props__info{
  opacity: 0;
  transition:opacity .5s;
}
.swiper-slide-active .item-props__info{
  opacity: 1;
  transform: opacity .5s;
  position: absolute;
  display: flex;
  flex-direction: column;  
  bottom: 50px;
  color:  white;
  font-weight: 100;
}
.slider-machine .item-props__name {
  text-transform: uppercase;
  font-family: 'Druk';
  font-weight: 1000;

}

.slider-machine .item-props__price {
  font-size: .8rem;
  font-family: Helvica;

}
.slider-machine .swiper-wrapper{
    align-items: center;
}

.slider-machine .swiper-slide {
  height: 450px;
  text-align: center;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img{
  transition: 0.8s ease-out;

}
.slider-machine .swiper-slide img {
    display: block;
    height: 15%;
    width: 100%;


    object-fit: contain;
  }
  .slider-machine .swiper-slide-next img {
    height: 30%;

  }

  .slider-machine .swiper-slide-active img {
    height: 100%;
  }
  

  