
:root {
  --layer-main-position: 0;
  --layer-back-position: 9;
  --layer-front-position: 10;

  --biggest-text: 22rem;
  --biggest-text-b: 12rem;
  --big-text: 3rem;
  --medium-text: 2rem;
  --small-text: 1.2rem;

  --color-text: #fff;
  --shadow-color-text: #ffffff1e;
}
@font-face {
    font-family: 'Druk';
    src: url('../../public/font/DrukWide-Bold.otf') format('opentype');
  }
@font-face {
    font-family: 'Bebas';
    src: url('../../public//font/bebas.otf') format('opentype');
}
@font-face {
  font-family: 'Helvica';
  src: url('../../public//font/helvica.otf') format('opentype');
}
@font-face {
  font-family: 'Helvica-strong';
  src: url('../../public//font/helvica-strong.ttf') format('opentype');
}
@font-face {
  font-family: 'Helvica-now';
  src: url('../../public//font/helvica-now.ttf') format('opentype');
}

  header.nav-bar{
    position: fixed;
    z-index: 100;
    height: 100px !important;
    width: 100vw;
    overflow: hidden;
    top: 0;
    left: 0;
    height: 0;
    display:  flex;
    align-items: center;
    justify-content: space-between;
  }

.nav-bar__side-item{
  height: 40px;
  width:  40px;
  margin: 0 15px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
}
  .nav-bar__logo {
    height: 70px;
    width: 180px;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    background-image: url("../media/icon/logo-full.png");
  }
  .nav-bar__shop {
    background-image: url("../media/icon/shop.png");
    height: 25px;
    width:  25px;
  }
  .nav-bar__side-menu {
    background-image: url("../media/icon/menu.png");

  }
  
  
  
body {
  padding: 0;
  margin: 0;
  overflow: hidden;
  background-color: rgb(0, 0, 0);
  font-family: 'Druk', sans-serif;
}
.hidden{
  display: none !important;
}

.anim-item {
  white-space: nowrap;
  position: absolute;
  color: var(--color-text);
  margin: 0;
  padding: 0;
}
/*CORTINAS*/
.cortain {
  position: fixed;
  z-index: 100;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100vw;
  height: 100vh;
}
.cortain .cortain--open {
  height: 0;
}
.cortain__sheet {
  transition: 1.3s all ease;
  width: inherit;
  height: 50vh;
  background-color: #000;
}
.cortain__top {
  top: 0;
}
.cortain__bottom {
  bottom: 0;
}
/*END CORTINAS*/
/*DEFUALT FRONT POSITIONS TEXT*/
.full-screen-container {
  width: 100vw;
  height: 100vh;
  position: fixed;
  outline: solid rgb(15, 71, 255) 2px;
}
.outline-text {
}
.shadow-text {
  color: var(--shadow-color-text);
}
.front-container {
  z-index: var(--layer-front-position);
}
.front-container__description-pack {
  top: 60%;
  left: 5%;
  font-size: var(--big-text);
  opacity: 0;
  line-height: 1;
}
.front-container__span-athenas3 {
  bottom: -45%;
  left: 20%;
  font-family: 'Bebas', sans-serif;
  font-size: var(--biggest-text);
  opacity: 0;
}
.front-container__span-athenas3-b {
  bottom: -15%;
  left: -10%;
  opacity: 0;
  transform: scale(1.2);
  font-family: 'Bebas', sans-serif;
  font-size: var(--biggest-text);
}
.front-container__span-fp-version {
  top: 25%;
  left: 30%;
  font-size: var(--small-text);
  opacity: 0;
}
.front-container__span-fs-version {
  top: 25%;
  right: 30%;
  font-size: var(--small-text);
  opacity: 0;
}
.front-container__span-hmi{
    font-size: var(--big-text);
    top: 40%;
    right: 5%;
    opacity: 0;
}
/*END DEFAULT FFRONT POSITIONS TEXTS*/
/*BACK POSITIONS TEXT*/
.back-container {
  z-index: var(--layer-back-position);
}
.back-container__description-food {
  font-size: var(--big-text);
  opacity: 0;
  top: 25%;
  left: 10%;
  line-height: 1;

}
.back-container__description-flow {
  text-align: center;
  font-size: var(--biggest-text-b);
  font-style: italic;
  font-family: fantasy;
  -webkit-text-stroke: #ffffff4e 2px;
  -webkit-text-fill-color: transparent;
  transform: translateY(80%)
}
.back-container__logo {
  top: 30%;
  left: 50%;
  height: 350px;
  transform:translateY(-50%) translateX(-50%) scale(1);
  opacity: 0;
}
.back-container__heavy{
    font-size: var(--big-text);
    opacity: 0;
    top: 65%;
    left: 5%;
}

/*END DEFAULT BACK POSITIOaNS TEXTS*/


/*IMAGE SCREEN ANIMATIONS*/
#screen-container{
    height: 100;
    width: 157px;
    color:  white;
    opacity: 0;
    transition: all 0.5s;
    background-color: rgba(255, 0, 0, 0.315);
    background-image: url("../media//backgrounds//screen-1.png");
    background-size: contain;
}


.industry, .proudly {
  position: absolute;
  top: -50%;
  left: -50%;
  transform: translate(50%, 50%);
  color: white;
  display: flex;
  padding-left: 80px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 100vh;
  width: 100vw;
}
.industry__title, .proudly__title  {
  font-size: 4rem;
  font-family: 'Druk';
  line-height: 4rem;
  margin-bottom: 40px;
}
.industry-corousel, .proudly-corousel {
  display: flex;
  flex-direction: row;
  width: 65vw;
  overflow: hidden;
}
.industry-corousel .patner__img, .proudly-corousel .slide{
  object-fit: contain;
  margin-right: 30px;
  max-height: 150px;
  height: 90px;
  width: 180px;
}
.slide img{
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.proudly-container{

  animation:  scroll 15s linear infinite;
  display: flex;
  flex-direction: row;
  width: calc(180px * 25);
}
.proudly-corousel{
  width: 65vw;
  height: 150px;

}
.industry, .proudly{
  opacity: 0;
}
.proudly{
  padding-top: 60px;
}

@keyframes scroll {
	0% { transform: translateX(0); }
	100% { transform: translateX(calc(-180px * 13))}
}

/*ARTICLES PAGE*/

.center-absolute{
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 95vw;
  height: 95vh;
}

.articles{
  top: -50%;
  opacity: 0;
  display: grid; /*grid*/
  place-content: center;
  grid-template-areas: 
  "header header"
  "main articles";
  grid-template-columns: 1fr  0.8fr;
  grid-template-rows: 80px  .7fr;
  gap: 10px 20px;
  padding: 5vw;
  color: white;
  font-family: 'Helvica';
}

.articles__header {
  grid-area: header;
  display:  flex;
  justify-content: space-between;
  align-items: center;
  border-bottom:  dashed white 1px;
}
.header__title {
  font-size: 2rem;
  font-family: 'Druk';
}
.header__more-btn {
  width: 180px;
  height: 45px;
  border:  solid whitesmoke;
  display: grid;
  place-content: center;
}
.articles__main-article {
  grid-area: main;
  position: relative;
}
.main-article__img {
  width: 100%;
  height: 100%;
}
.main-article__img img{
  max-width: 100%;
  height: 100%;
  object-fit: cover;

}
.main-article__footer{
  position: absolute;
  width:inherit;
  height: 20px;
  bottom: -20px;
  left: 0;
  display:  flex;
}
.footer__span {
  width: 500px;
}
.footer__date {
  width: 120px;
  font-family: 'Bebas';
}
.articles__wrapper {
  grid-area: articles;
}
.wrapper__list-article {
  height: calc(100% / 2.1);
  margin-bottom: 20px;
  display: grid;
  gap: 10px;
  grid-template-areas: 
  "preview title"
  "preview span"
  "preview info";
  grid-template-rows: 50px 20px 1fr;
  grid-template-columns: 1fr 1fr;
  font-family: unset;
  font-size: 0.6rem;
}
.list-article__preview{
  grid-area: preview;
  width: 100%;
  height: 100%;
}
.list-article__preview img{
  max-width:100%;
  height: 100%;
  
}
.list-article__title {
  grid-area: title;
  width: 100%;
  overflow: hidden;
  font-size: 1rem;
  color: whitesmoke;

}
.list-article__span {
  grid-area: span;
  font-family: 'Bebas';
  font-size: 1rem;
  width: 100%;
  height: 100%;
}
.list-article__description {
  grid-area: info;
  width: 100%;
  height: 100%;
  overflow: auto;
  font-family: 'Helvica-strong';
  font-size: .9rem;
  line-height: 0.9rem;
  color: whitesmoke;
}


/*QUATE SECTION*/
.contact-us {
  top: 200%;
  opacity: 0;
  display:  grid;/*grid*/
  place-content: center;
  place-items: center;
  color:  white;
  text-align: center;
}
.contact-us__title {
  width: 60vw;
  height: fit-content;
  font-size: 3.5rem;
  margin-bottom: 25px;
  line-height: 3.1rem;
}
.contact-us__quote-btn {
  width: fit-content;
  border: dashed whitesmoke 1px;
  padding: 15px 25px;
}


/*FOOOTER HERE*/
#footer{
  top:100%;
  opacity: 0;
  display:  grid;/*grid*/
  place-content: center;
}
.footer-container {
  width:  75vw;
  height: 75vh;
  font-size: 0.8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  color:  #fff;
  text-align: center;
  font-family: 'Helvica';
}
.footer-container__logo {
  width: 100px;
  height: 100px;
  background-image: url("../media/icon/logo-anim.gif");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  margin-bottom: 100px;
}

.footer-container__links {
  list-style: none;
  display:  flex;
  justify-content: center;
  padding: 25px 0;
  border-bottom: dashed 1px rgb(206, 206, 206);
  width: 70%;
  height: 5px;
}
.links__item {
  margin: 0 5px;
  height: fit-content;
  width: auto;
}
.links__item a{
  color:  rgb(176, 176, 176);
  text-decoration: none;
}
.footer-container__credits {
  width: 65vw;
  height: 30px;
}
.footer-container__card-preview {
  height: 80px;
  width: 160px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url("../media/icon/cards.png");
}

#idle-video-container{
  width: 100%;
  height: 100%;
  position: fixed;
  opacity: 0;
  transition: all 1.5s;
  background-color: black;
}
#idle-video-container.show{
  z-index: 10;
  opacity: 1;
  transition: all 1.5s;
  background-color: transparent;
}
div.dg.ac{
  z-index: 200;
  display:  none;
}